import React, { useCallback, useEffect } from 'react';
import ImageEditor from '@toast-ui/react-image-editor';
import 'tui-image-editor/dist/tui-image-editor.css';


export const ImageUploader = () => {
    let cropped = false;
    const [cropping, setCropping] = React.useState();

  const [imageEditor, setImageEditor] = React.useState();
  const editorRef = useCallback((ref) => {
    if (ref && !imageEditor) {
      setImageEditor(ref.getInstance());
    }
  }, []);

  const onMouseUp = () => {
      if (cropping) {
        if (!cropped) {
            imageEditor.crop(imageEditor.getCropzoneRect());
            imageEditor.deactivateAll();
            imageEditor.stopDrawingMode();
            setCropping(false);
            const buttons = document.getElementsByClassName("tie-btn-crop");
            if (buttons && buttons[0]) {
                const button = buttons[0];
                button.click();
            }
          }

      }
  }

  React.useEffect(() => {
    if (cropping) window.addEventListener("mouseup", onMouseUp);
    return () => window.removeEventListener("mouseup", onMouseUp);
    }, [cropping]);


  if (imageEditor) {
      imageEditor.on('objectActivated', (props) => {

          if (props.type === 'cropzone') {
              setCropping(true);
          }
          
        });
    //   imageEditor.on('objectAdded', (props) => console.log(props,'OBJECTADDED'));
    //   imageEditor.on('objectMoved', (props) => console.log(props, 'OBJECTMOVED'));
    //   imageEditor.on('mousedown', (props) => console.log(props, 'MOUSEDOWN'));
    //   imageEditor.on('mouseup', (props) => console.log(props, 'MOUSEUP'));
  }

  return (
    <div style={{height: '100vh', paddingTop: '160px'}}>
      <ImageEditor
        style={{ position: 'relative' }}
        ref={editorRef}
        includeUI={{
          loadImage: {
            name: 'Floor Plan',
          },
            menu: ['crop', 'flip', 'rotate'],
          menuBarPosition: 'bottom',
        }}
        usageStatistics={false}
      />
    </div>
  );
};

export default ImageUploader;
